import React, { Component } from 'react'
import _ from 'lodash'
import {Container,Divider,Dropdown,Grid,Header,Icon,Image,List,Menu,Segment,Visibility,Button,Message} from 'semantic-ui-react'
import MediaQuery from 'react-responsive'
import {Profile} from './Profile.js'
import {Resume} from './Resume.js'
import {Works} from './Works.js'
import './Body.css'
import {CSSTransitionGroup} from 'react-transition-group'

const greetingWrapperUp = {marginTop:"-5%"}//used for shifting the greeting upwards
const greetingWrapperDefault = {marginTop:"2.5%"}//used for shifting the greeting to default position
const greetingExpanded = {marginTop:"5.2%",marginLeft:"39%",position:"fixed", zIndex:100}
const buttonResumeExpanded = {marginTop:"17%", marginLeft:"0%"}
const buttonGithubExpanded = {marginTop:"-0.38%", marginLeft:"2%"}
const aboutMeExpanded = {marginTop:"2.5%", fontSize:"120%"}
const menuButton1Default = {marginLeft: "60%", fontFamily: "Montserrat, sans-serif", fontSize: "250%"}
const menuButton2Default = {fontFamily: "Montserrat, sans-serif", fontSize: "250%"}
const menuButton3Default = {marginRight: "60%", fontFamily: "Montserrat, sans-serif", fontSize: "250%"}

export class Body extends Component {
  constructor(props){
  	super(props)
  	this.handleHomeClick = this.handleHomeClick.bind(this)
  	this.handleExpClick = this.handleExpClick.bind(this)
  	this.handlePortfolioClick = this.handlePortfolioClick.bind(this)
	this.state = {
		profile: true,
		resume: false,
		works: false
	}
  }

  handleHomeClick(){
  	this.setState({profile: true, resume: false, works: false})
  }

  handleExpClick(){
  	this.setState({profile: false, resume: true, works: false})
  }

  handlePortfolioClick(){
  	this.setState({profile: false, resume: false, works: true})
  }
//  stickOverlay = () => this.setState({ overlayFixed: true })
  render() {
  	const menuWrapperClass = this.state.profile?"content-menu-default":"content-menu-experience"
  	const menuItemProfileClass = this.state.profile?"content-menu-item-selected":"content-menu-item"
  	const menuItemResumeClass = this.state.resume?"content-menu-item-selected":"content-menu-item"
  	const menuItemPortfolioClass = this.state.works?"content-menu-item-selected":"content-menu-item"
  	const footerWrapperClass = this.state.profile?"content-footer-default":"content-footer-experience"

    return (
      <div className="web-body">
      	{/*<Segment basic style={{height:"80%",width:"100%",position:'auto',backgroundColor: '#f6eabe'}}>
      		<Segment basic className={greetingWrapperClass}> {/*shift this wrapper for animation}
	      		<Image
				    floated='left'
				    circular={true}
				    src='data:image/jpeg;base64,'
				    className="profile-content-image"
				/>
				<Segment basic fluid floated="right" className="profile-greeting-segment-default">
					<p className="profile-greeting">Hey!<br />I'm <b>Wei Zheng</b></p>
				</Segment>
			</Segment> */}
			{/*
			<Segment basic className="profile-buttons-segment"> {/*shift this wrapper for animation
				<Button size="big" inverted color="orange" className="profile-buttons-resume" style={buttonResumeExpanded}>
					Download Resume
				</Button>
				<Button size="big" inverted color="orange" style={buttonGithubExpanded} animated>
					<Button.Content visible>Github</Button.Content>
					<Button.Content hidden><Icon name='github' /></Button.Content>
				</Button>

				<Container text textAlign='center' style={aboutMeExpanded}>
					<text>I love just about anything IT, from design to implementation, IoT products to Web Apps,<br /></text>
					<text>Assembly Language to Java --  its all so intriguing.</text><br /><br />
					<text>I like designing products and then developing and implementing them, and hence, strongly believe that good design skills complement programming expertise,
					and together they can better express one's creativity.</text>
				</Container>
			</Segment>*/}
			<CSSTransitionGroup transitionName="profile" transitionEnterTimeout={500} transitionLeaveTimeout={300}
			transitionAppear={true} transitionAppearTimeout={500}>
				{this.state.profile && <Profile />}
			</CSSTransitionGroup>

		{/*Buttons*/}
		<Segment basic className="content-menu-experience">
			<Menu text widths={3} >
				<Menu.Item className={menuItemProfileClass} fitted active={this.state.profile} onClick={this.handleHomeClick}><text style={menuButton1Default}>About Me</text></Menu.Item>
				<Menu.Item className={menuItemResumeClass} fitted active={this.state.resume} onClick={this.handleExpClick}><text style={menuButton2Default}>Relevant Experience</text></Menu.Item>
				<Menu.Item className={menuItemPortfolioClass} fitted onClick={this.handlePortfolioClick}><text style={menuButton3Default}>Portfolio</text></Menu.Item>
			</Menu>		
		</Segment>


		{this.state.resume && <Resume />}
		{this.state.works && <Works />}
		{/*Footer(static component) */}
		<div className={footerWrapperClass} style={{backgroundColor:"#E9BE4B"}}></div>
      </div>
    )
  }
}